/*
    Module Rules make up the smaller, groupable areas of the site.

    Some examples are callouts, calls-to-action (cta), sidebars, and so on.
    They should be named in a way that is independent of the page context, making
    them reusable throughout the site. This is the point of modularity.
*/
.btn-fill:hover,
.btn-fill:focus {
  box-shadow: inset 0 0 0 2em var(--hover); }

.btn-fill {
  color: var(--color);
  transition: 0.25s;
  background: none;
  border: 2px solid;
  font: inherit;
  line-height: 1;
  padding: 1rem 3rem; }
  @media screen and (min-width: 768px) {
    .btn-fill {
      padding: 1rem 2.5rem;
      margin-top: 12px; } }
  .btn-fill:hover, .btn-fill:focus {
    border-color: var(--hover);
    color: #fff; }

.btn-fill {
  --color: #a972cb;
  --hover: #cb72aa; }

